#genre {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  /* overflow-x: scroll; */
}

#discover {
  font-family: "Roboto", sans-serif;
  font-size: 2.75rem;
  font-weight: 700;
  letter-spacing: 1px;
  padding: 2rem 0;
}

#recent {
  font-family: "Roboto", sans-serif;
  font-size: 0.8rem;
  font-weight: 600;
  letter-spacing: 2px;
  padding: 0 0 1rem 0;
  color: rgba(255, 255, 255, 0.5);
  text-transform: uppercase;
}